import React from 'react'
import { Text } from 'theme-ui'
import TextList from '@components/TextList'
import { FaRegClock } from 'react-icons/fa'
import rv from '@components/utils/buildResponsiveVariant'


const options = { year: 'numeric', month: 'long', day: 'numeric' };

const CardFooterInfo = ({ variant, date, timeToRead }) => (

  <TextList nowrap>

    {new Date(date).toLocaleDateString('ar-ma', options) && <Text sx={{ variant: rv(variant, 'date') }}>{new Date(date).toLocaleDateString('ar-ma', options)}</Text>}
    {/* {timeToRead && (
      <Text sx={{ variant: rv(variant, 'timeToRead') }}>
        <FaRegClock css={{ verticalAlign: `middle` }} /> {timeToRead} min
      </Text>
    )} */}
  </TextList>
)

export default CardFooterInfo
